/* user custom */
.top-nav {
  display: none !important;
}
#fp-nav ul li,
.fp-slidesNav ul li {
  width: 20px;
  height: 20px;
}
#fp-nav ul li a span,
.fp-slidesNav ul li a span {
  transform: translate(-50%, -50%);
}
#fp-nav ul li a span,
#fp-nav ul li:hover a span,
.fp-slidesNav ul li a span,
.fp-slidesNav ul li:hover a span,
#fp-nav ul li a.active span,
#fp-nav ul li:hover a.active span,
.fp-slidesNav ul li a.active span,
.fp-slidesNav ul li:hover a.active span {
  width: 20px;
  height: 20px;
  margin: 0;
}
#fp-nav ul li a span, .fp-slidesNav ul li a span{
  background-color: #ccc;
  opacity: 0.5;
  border:2px solid #ccc;
}
#fp-nav ul li a span,
#fp-nav ul li:hover a span,
.fp-slidesNav ul li a span,
.fp-slidesNav ul li:hover a span {
  width: 10px;
  height: 10px;
}

#fp-nav {
  margin-top: 0;
  transform: translate(0, -50%);
}

#section1 {
  color: #fff;
}

.fp-section {
  text-align: center;
}

.fp-slide {
  height: 50vh;
}

button {
  padding: 0.93em 1.87em;
  background: #35495e;
  border-radius: 5px;
  border-color: transparent;
  display: block;
  color: #fff;
  margin: 0 auto;
  cursor: pointer;
  font-size: 0.85em;
}

.section {
  height: 100vh;
}

.section h3 {
  font-size: 5em;
  text-align: center;
  color: #fff;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.fp-warning,
.fp-watermark a {
  display: none !important;
}
.fp-controlArrow.fp-prev{left:50px;}
.fp-controlArrow.fp-next{right:50px;}


.section1{background:#fff url('https://melllow.co.kr/vwHomePage/images/CDN/main/main_bg_01.jpg') no-repeat right bottom;background-size:70%;}